import React from 'react';
import './App.css';
import BigCanvas from './canvas'
import ToolBox from './toolbox'
import { element } from 'prop-types';

export default class App extends React.Component {

  constructor(props) {
    super(props);
    this.selectedIndex = 0
    this.hideToolbox();
  }

  componentDidMount() {
    //{ 'canvas':canvas } 不行?
    this.bc = new BigCanvas({ canvasId: 'canvas', showToolbox: this.showToolbox.bind(this), hideToolbox: this.hideToolbox.bind(this) });

    document.addEventListener('keydown', event => {
      //屏蔽浏览器响应空格键的默认行为
      if (event.which === 32) {
        event.preventDefault();
        event.stopPropagation();
      }
    });

    //阻止触控板的放大缩小
    //设置passive = true
    //event.deltaY 放大为负 缩小为正
    window.addEventListener('mousewheel', event => {
      if (event.ctrlKey === true) {
        event.preventDefault();
        event.stopPropagation();
        this.bc.zoom(event);
      }
    }, { passive: false });

    //右键
    window.addEventListener('contextmenu', event => {
      event.preventDefault();
      event.stopPropagation();
      const objects = this.bc.getActiveObjects();
      if (objects.length > 0) {
        objects.forEach(element => {
          if (element.get('type') !== 'textbox') {
            this.showToolbox(event.x, event.y);
          }
        })
      } else {
        this.hideToolbox();
      }
    });

    this.setState({
      canvas: this.bc,
    });
  }

  showToolbox(x, y) {
    this.setState({
      toolbox_x: x,
      toolbox_y: y,
      toolbox_show: true,
    });
  }

  hideToolbox() {
    this.setState({
      toolbox_show: false,
    });
  }

  render() {
    const { toolbox_x, toolbox_y, toolbox_show } = this.state || {};
    return (
      <div className='container'>
        <canvas className='canvas' id='canvas' />
        <ToolBox className='toolbox' id='toolbox' style={{ left: toolbox_x, top: toolbox_y }} currentPostColor={this.bc ? this.bc.currentPostColor : ''} onClick={(index, color) => {
          this.bc.currentPostColor = color;
          this.bc.setActiveObjectsPostColor(color);
          this.setState({
            canvas: this.bc,
          });
        }} />
      </div>
    )
  }
}
